import React from 'react'
import './pricing.css'

const Pricing = () => {
    return (
        <div className="pricing" id="pricing">
            <div clasName="pricing-title">Cennik:</div>
            <div className="pricing-main">
                <div className="pricing-content">
                    <div className="pricing-up">
                    Cennik sali prób i studio nagrań Próbówka Music House Wrocław (niezależnie od wybranej sali). <br></br>
                    Potrzebujesz rachunku - nie ma sprawy - działamy legalnie.<br></br>
                    </div>
                    <div className="pricing-left">
                    1 godzina = 35 zł <br></br>
                    2 godziny = 60 zł <br></br>
                    3 godziny = 80 zł <br></br>
                    
                    pakiet 8h / miesiąc (płatność z góry, brak możliwości odwołania próby): 220 zł <br></br>
                    pakiet 12h / miesiąc (płatność z góry, brak możliwości odwołania próby): 300 zł <br></br>
                    </div>
                    <div className="pricing-right"></div>

                    nagrywanie próby (z sound-checkiem przeprowadzonym przez realizatora : 45 zł /h min. 2h
                    nagranie live 45 minut w salce (możliwość zaproszenia publiki) + mix i mastering trzech wybranych utworów<br></br> 
                    + dogrywka wokalu w razie potrzeby + pliki video z kamer + wrzucenie nagrania na profil sali prób na serwisach streamingowych: 750 zł przy standardowym składzie zespołu (2x gitara, bas, wokal, perkusja)<br></br>
                    Przykładowe nagranie: <a href="https://soundcloud.com/izolacja">https://soundcloud.com/izolacja</a>
                    studio: 50 zł / h <br></br> 
                    cały dzień (10h) 500 zł<br></br> 
                
                    mix i mastering od 100 zł utwór<br></br>
                    Przykładowe nagrania: <a href="https://soundcloud.com/piotr-maczynski"> https://soundcloud.com/piotr-maczynski</a>
                </div>
            </div>
        </div>
    )
}

export default Pricing
